import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
  CFormLabel,
  CCardFooter,
  CModal,
  CModalHeader,
  CModalFooter,
  CModalTitle,
  CModalBody,
} from "@coreui/react-pro";
import React, { useEffect, useState } from "react";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import { Link } from "react-router-dom";
import {
  faSave,
  faTimesCircle,
  faCheckCircle,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Formik, Form, Field, FieldArray, ErrorMessage } from "formik";
import * as Yup from "yup";
import Loader from "src/loader/Loader";
import EVFormikControl from "src/ev_formik_controls/EVFormikControl";
import EDU_AXIOS from "src/helpers/edu_axios";
import notify from "src/helpers/notify";
import { useHistory } from "react-router-dom";
import MaterialTable from "material-table";

const add_sections = (props) => {
  let school_id = props.match.params.school_id;
  let [visible, setVisible] = useState(false);
  let [sch_class, schSetClass] = useState([]);
  let [db_sections, setDbSections] = useState([]);
  let history = useHistory();
  // open cancle modal box
  let openModalBox = () => {
    setTimeout(() => setVisible(true), 0);
  };

  // close cancle modal box
  let closeModalBox = () => {
    setTimeout(() => setVisible(false), 0);
  };

  let sections = [
    { text: "A", value: "A" },
    { text: "B", value: "B" },
    { text: "C", value: "C" },
    { text: "D", value: "D" },
    { text: "E", value: "E" },
    { text: "F", value: "F" },
    { text: "G", value: "G" },
    { text: "H", value: "H" },
    { text: "I", value: "I" },
    { text: "J", value: "J" },
    { text: "K", value: "K" },
    { text: "L", value: "L" },
    { text: "M", value: "M" },
    { text: "N", value: "N" },
    { text: "O", value: "O" },
  ];
  useEffect(() => {
    EDU_AXIOS.get(`${process.env.REACT_APP_API_PATH}/cm/get-class/${school_id}`)
      .then((response) => {
        if (!!response.data.data) {
          schSetClass(response.data.data.classes);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  let get_sections = () => {
    EDU_AXIOS.get(
      `${process.env.REACT_APP_API_PATH}/cm/get-sections/${school_id}`
    )
      .then((response) => {
        setDbSections(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  // get sections
  useEffect(() => {
    get_sections();
  }, []);

  // formik params
  let initialValues = {
    sc_class: "",
    sections: [],
  };
  // validation rules of the form fields
  const validationSchema = Yup.object().shape({
    sc_class: Yup.string().required("Class is required"),
    sections: Yup.array().min(1).of(Yup.string().trim().required()),
  });
  // submit form
  const onSubmit = async (values, { resetForm }) => {
    await EDU_AXIOS.post(
      process.env.REACT_APP_API_PATH + "/cm/create-sections",
      {
        school_id: school_id,
        sc_class: values.sc_class,
        sc_sections: values.sections,
      }
    )
      .then(async (res) => {
        resetForm({ values: "" });
        get_sections();
        notify("success", res.data.message);
      })
      .catch((err) => {
        notify("error", err);
      });
  };
  const columns = [
    {
      title: "Class",
      field: "class",
    },
    {
      title: "Sections",
      field: "sections",
      render: (data) => {
        return data.sections.join();
      },
    },
  ];
  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          <CRow>
            <CCol md="8" lg="6" xl="8">
              <h5>Create Sections</h5>
            </CCol>
          </CRow>
          <p className="text-muted">Add sections</p>
          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={onSubmit}
          >
            {(formik) => (
              <Form className="row g-3">
                <CCol md={6}>
                  <CFormLabel htmlFor="class_from" className="required-field">
                    Select Class
                  </CFormLabel>
                  <EVFormikControl
                    control="select"
                    name="sc_class"
                    id="sc_class"
                    value={formik.values.sc_class}
                    placeholder="Select class..."
                    options={sch_class}
                  />
                </CCol>
                <CCol md={6}></CCol>
                <CCol md={12}>
                  <CFormLabel htmlFor="class_from" className="required-field">
                    Select Sections
                  </CFormLabel>
                  <br />
                  <EVFormikControl
                    control="checkbox"
                    name="sections"
                    id="sections"
                    options={sections}
                    inline
                  />
                </CCol>

                <CCardFooter>
                  <CButton block color="light" onClick={openModalBox}>
                    <FontAwesomeIcon icon={faTimesCircle} /> Cancel
                  </CButton>
                  <CButton
                    block
                    color="dark"
                    type="submit"
                    disabled={
                      formik.isSubmitting || !formik.dirty || !formik.isValid
                    }
                    style={{ marginLeft: "15px" }}
                  >
                    {formik.isSubmitting ? (
                      <Loader />
                    ) : (
                      <>
                        <FontAwesomeIcon icon={faSave} /> Save
                      </>
                    )}
                  </CButton>
                </CCardFooter>
                <CModal visible={visible} onDismiss={closeModalBox}>
                  <CModalHeader onDismiss={closeModalBox}>
                    <CModalTitle>
                      <h5>Cancel</h5>
                    </CModalTitle>
                  </CModalHeader>

                  <CModalBody>Do You Want To Cancel ?</CModalBody>
                  <CModalFooter>
                    <CButton color="light" onClick={closeModalBox}>
                      <FontAwesomeIcon icon={faTimesCircle} /> No
                    </CButton>
                    <Link to="/">
                      <CButton color="dark">
                        <FontAwesomeIcon icon={faCheckCircle} /> Yes
                      </CButton>
                    </Link>
                  </CModalFooter>
                </CModal>
              </Form>
            )}
          </Formik>
          {/*Render data into table*/}
          <MaterialTable
            title={"All Sections"}
            data={db_sections}
            columns={columns}
            onSelectionChange={(rows) => setSelectedRows(rows)}
            options={{
              search: true,
              paging: true,
              filtering: false,
              exportButton: true,
              actionsColumnIndex: -1,
              rowStyle: (x) => {
                if (x.tableData.id % 2) {
                  return { backgroundColor: "#f9f9f9" };
                }
              },
            }}
          />
          {/*End table*/}
        </CCardBody>
      </CCard>
    </CCardGroup>
  );
};

export default add_sections;
